@import "../scss/variables";

.dark-mode {
	a {
		color: #d5dae2;
	}

	.nav-sidebar li ul {
		background: #eeeeee;
	}
}
.dark-mode #back-to-top:hover {
	background: #272d47;
}
body.dark-mode {
	color: #d5dae2;
	background: $dark-theme;
}

.dark-mode {
	a:hover {
		text-decoration: none;
	}

	caption {
		color: #505662;
	}

	.bcolor {
		background-color: #cf4c3a !important;
	}

	.bcolor1 {
		background-color: #d6124a !important;
	}

	hr {
		border-top: 1px solid rgba(255, 255, 255, 0.04);
	}

	mark,
	.mark {
		background-color: #fcf8e3;
	}

	.list-unstyled li {
		border-bottom: 1px solid rgba(255, 255, 255, 0.04);
	}

	#sidebar .list-unstyled li {
		border-bottom: 1px solid rgba(255, 255, 255, 0.03);
	}

	.blockquote-footer {
		color: #868e96;
	}

	.img-thumbnail {
		background-color: #272d47;
		border: 1px solid rgba(255, 255, 255, 0.04);
	}

	.figure-caption {
		color: #868e96;
	}

	kbd {
		color: $white;
		background-color: #343a40;
	}

	.table th {
		border-top: 1px solid rgba(255, 255, 255, 0.04);
	}

	.text-wrap table {
		th,
		td {
			border-top: 1px solid rgba(255, 255, 255, 0.04);
		}
	}

	.table-hover tbody tr:hover {
		color: $white;
	}

	.border {
		border: 1px solid rgba(255, 255, 255, 0.04);
	}

	.table thead th,
	.text-wrap table thead th {
		border-bottom: 1px solid rgba(255, 255, 255, 0.04);
	}

	.table tbody + tbody,
	.text-wrap table tbody + tbody {
		border-top: 2px solid rgba(255, 255, 255, 0.1);
	}

	.table .table,
	.text-wrap table .table,
	.table .text-wrap table {
		background-color: rgba(32, 34, 60, 0.5);
	}

	.text-wrap {
		.table table,
		table table {
			background-color: rgba(32, 34, 60, 0.5);
		}
	}

	.table-bordered,
	.text-wrap table,
	.table-bordered th,
	.text-wrap table th,
	.table-bordered td,
	&.text-wrap table td {
		border: 1px solid rgba(255, 255, 255, 0.04);
	}

	.table-striped tbody tr:nth-of-type(odd) {
		background-color: rgba(0, 0, 0, 0.02);
	}

	.table-hover tbody tr:hover {
		background-color: rgba(70, 127, 207, 0.06);
	}

	.table-primary {
		background-color: #d5cee6;

		> {
			th,
			td {
				background-color: #d5cee6;
			}
		}
	}

	.table-hover .table-primary:hover {
		background-color: #b7cded;

		> td {
			background-color: #b7cded;
		}
	}
}

@media (min-width: 992px) {
	.dark-mode {
		#sticky {
			color: #fff;
		}

		.horizontalMenu > .horizontalMenu-list > li > ul.sub-menu > li > ul.sub-menu {
			background-color: #272d47;
			border: 1px solid rgba(255, 255, 255, 0.04);
			box-shadow: 0 10px 15px rgba(15, 17, 29, 0.9);
		}
	}
}

.table-hover .table-primary:hover > th {
	background-color: #b7cded;
}

.dark-mode {
	&.table-secondary {
		background-color: #f72d66;
	}

	.table-secondary > {
		th,
		td {
			background-color: #f72d66;
		}
	}

	.table-hover .table-secondary:hover {
		background-color: #cfd2d6;

		> {
			td,
			th {
				background-color: #f72d66;
			}
		}
	}

	.table-light {
		background-color: #fdfdfe;

		> {
			th,
			td {
				background-color: #fdfdfe;
			}
		}
	}

	.table-hover .table-light:hover {
		background-color: #ececf6;

		> {
			td,
			th {
				background-color: #ececf6;
			}
		}
	}

	.table-dark {
		background-color: #c6c8ca;

		> {
			th,
			td {
				background-color: #c6c8ca;
			}
		}
	}

	.table-hover .table-dark:hover {
		background-color: #b9bbbe;

		> {
			td,
			th {
				background-color: #b9bbbe;
			}
		}
	}

	.table-active {
		background-color: rgba(0, 0, 0, 0.04);

		> {
			th,
			td {
				background-color: rgba(0, 0, 0, 0.04);
			}
		}
	}

	.table-hover .table-active:hover {
		background-color: rgba(0, 0, 0, 0.04);

		> {
			td,
			th {
				background-color: rgba(0, 0, 0, 0.04);
			}
		}
	}

	.table .thead-dark th,
	.text-wrap table .thead-dark th {
		color: rgba(32, 34, 60, 0.5);
		background-color: #212529;
		border-color: #32383e;
	}

	.table .thead-light th,
	.text-wrap table .thead-light th {
		color: #c4c9d6;
		background-color: rgba(255, 255, 255, 0.1);
		border-color: rgba(255, 255, 255, 0.1);
	}

	.table-dark {
		color: rgba(32, 34, 60, 0.5);
		background-color: #212529;

		th,
		td,
		thead th {
			border-color: #32383e;
		}

		&.table-striped tbody tr:nth-of-type(odd) {
			background-color: rgba(255, 255, 255, 0.05);
		}

		&.table-hover tbody tr:hover {
			background-color: rgba(255, 255, 255, 0.075);
		}
	}
}

@media (max-width: 991px) {
	.dark-mode {
		.animated-arrow.hor-toggle {
			background: #2f3655;
			box-shadow: 0px 2px 3px rgba(4, 4, 7, 0.1);
			border: 1px solid rgba(255, 255, 255, 0.04);
		}

		.horizontalMenu > .horizontalMenu-list {
			background: #272d47;
		}
		.horizontalMenu > .horizontalMenu-list > li > a{
			border-bottom-color: #ffffff0d;
		}
	}
}

/*------- Forms -------*/

.h-100vh.bg-primary.dark-mode .input-group .form-control {
	border: 1px solid #272d47;

	&:focus {
		border: 1px solid #272d47;
	}
}

.dark-mode {
	.form-control {
		color: rgba(255, 255, 255, 0.8);
		background-color: #272d47;
		border: 1px solid rgba(255, 255, 255, 0.1);
	}

	.select2-container--default {
		.select2-selection--single {
			background-color: #272d47;
			border: 1px solid rgba(255, 255, 255, 0.1) !important;
		}

		&.select2-container--focus .select2-selection--multiple {
			color: #cfd5da;
			background-color: #272d47;
			border-color: #4eceb2;
			box-shadow: 0 0 0 2px rgba(70, 127, 207, 0.25);
		}

		.select2-selection--multiple {
			background-color: #272d47;
			border: 1px solid rgba(255, 255, 255, 0.04) !important;
		}

		.select2-search--dropdown .select2-search__field {
			border: 1px solid rgba(255, 255, 255, 0.04) !important;
			background: #272d47;
			color: $white;
		}

		.select2-selection--multiple .select2-selection__choice {
			background-color: rgba(32, 34, 60, 0.5) !important;
			border: 1px solid rgba(255, 255, 255, 0.1) !important;
		}
	}

	.sp-replacer {
		&:hover,
		&.sp-active {
			border: 1px solid rgba(255, 255, 255, 0.1);
		}
	}

	.form-control {
		&:focus {
			color: rgba(255, 255, 255, 0.6);
			background-color: #272d47;
		}

		&::-webkit-input-placeholder,
		&::-moz-placeholder,
		&:-ms-input-placeholder,
		&::-ms-input-placeholder {
			color: #6e7289;
			opacity: 1;
		}

		opacity: 1;

		&:disabled,
		&[readonly] {
			background-color: #232543;
			opacity: 1;
		}
	}

	.select2-container--default .select2-selection--single .select2-selection__rendered {
		color: #a5a7b1;
	}

	select.form-control:focus::-ms-value {
		color: #c4c9d6;
		background-color: #272d47;
	}

	.form-control-plaintext {
		color: #c4c9d6;
		border: solid transparent;
		border-width: 1px 0;
	}

	.form-check-input:disabled ~ .form-check-label {
		color: #505662;
	}

	.valid-feedback {
		color: #2dce89;
	}

	.valid-tooltip {
		color: $white;
		background-color: rgba(94, 186, 0, 0.8);
	}

	.was-validated .form-control:valid,
	.form-control.is-valid,
	.was-validated .custom-select:valid,
	.custom-select.is-valid {
		border-color: #2dce89;
	}

	.was-validated .form-control:valid:focus,
	.form-control.is-valid:focus,
	.was-validated .custom-select:valid:focus,
	.custom-select.is-valid:focus {
		border-color: #2dce89;
		box-shadow: 0 0 0 2px rgba(94, 186, 0, 0.25);
	}

	.was-validated .form-check-input:valid ~ .form-check-label,
	.form-check-input.is-valid ~ .form-check-label,
	.was-validated .custom-control-input:valid ~ .custom-control-label,
	.custom-control-input.is-valid ~ .custom-control-label {
		color: #2dce89;
	}

	.was-validated .custom-control-input:valid ~ .custom-control-label::before,
	.custom-control-input.is-valid ~ .custom-control-label::before {
		background-color: #9eff3b;
	}

	.was-validated .custom-control-input:valid:checked ~ .custom-control-label::before,
	.custom-control-input.is-valid:checked ~ .custom-control-label::before {
		background-color: #78ed00;
	}

	.was-validated .custom-control-input:valid:focus ~ .custom-control-label::before,
	.custom-control-input.is-valid:focus ~ .custom-control-label::before {
		box-shadow: 0 0 0 1px rgba(32, 34, 60, 0.5), 0 0 0 2px rgba(94, 186, 0, 0.25);
	}

	.was-validated .custom-file-input:valid ~ .custom-file-label,
	.custom-file-input.is-valid ~ .custom-file-label {
		border-color: #2dce89;
	}

	.was-validated .custom-file-input:valid:focus ~ .custom-file-label,
	.custom-file-input.is-valid:focus ~ .custom-file-label {
		box-shadow: 0 0 0 2px rgba(94, 186, 0, 0.25);
	}

	.invalid-feedback {
		color: #dc0441;
	}

	.invalid-tooltip {
		color: $white;
		background-color: rgba(205, 32, 31, 0.8);
	}

	.was-validated .form-control:invalid,
	.form-control.is-invalid,
	.was-validated .custom-select:invalid,
	.custom-select.is-invalid {
		border-color: #dc0441;
	}

	.was-validated .form-control:invalid:focus,
	.form-control.is-invalid:focus,
	.was-validated .custom-select:invalid:focus,
	.custom-select.is-invalid:focus {
		border-color: #dc0441;
		box-shadow: 0 0 0 2px rgba(205, 32, 31, 0.25);
	}

	.was-validated .form-check-input:invalid ~ .form-check-label,
	.form-check-input.is-invalid ~ .form-check-label,
	.was-validated .custom-control-input:invalid ~ .custom-control-label,
	.custom-control-input.is-invalid ~ .custom-control-label {
		color: #dc0441;
	}

	.was-validated .custom-control-input:invalid ~ .custom-control-label::before,
	.custom-control-input.is-invalid ~ .custom-control-label::before {
		background-color: #ec8080;
	}

	.was-validated .custom-control-input:invalid:checked ~ .custom-control-label::before,
	.custom-control-input.is-invalid:checked ~ .custom-control-label::before {
		background-color: #e23e3d;
	}

	.was-validated .custom-control-input:invalid:focus ~ .custom-control-label::before,
	.custom-control-input.is-invalid:focus ~ .custom-control-label::before {
		box-shadow: 0 0 0 1px rgba(32, 34, 60, 0.5), 0 0 0 2px rgba(205, 32, 31, 0.25);
	}

	.was-validated .custom-file-input:invalid ~ .custom-file-label,
	.custom-file-input.is-invalid ~ .custom-file-label {
		border-color: #dc0441;
	}

	.was-validated .custom-file-input:invalid:focus ~ .custom-file-label,
	.custom-file-input.is-invalid:focus ~ .custom-file-label {
		box-shadow: 0 0 0 2px rgba(205, 32, 31, 0.25);
	}

	.btn {
		&:focus,
		&.focus {
			box-shadow: 0 0 0 2px rgba(194, 197, 199, 0.25);
		}
	}

	.btn-light {
		color: #d5dae2;
		background-color: #20223c;
		border-color: #20223c;

		&:hover {
			color: #d5dae2;
			background-color: #20223c;
			border-color: #20223c;
		}

		&:focus,
		&.focus {
			box-shadow: 0 0 0 2px rgba(248, 249, 250, 0.1);
		}

		&.disabled,
		&:disabled {
			color: #d5dae2;
			background-color: #20223c;
			border-color: #20223c;
		}

		&:not(:disabled):not(.disabled) {
			&:active,
			&.active {
				color: #d5dae2;
				background-color: #20223c;
				border-color: #20223c;
			}
		}
	}

	&.show > .btn-light.dropdown-toggle {
		color: #d5dae2;
		background-color: #20223c;
		border-color: #20223c;
	}

	.btn-light:not(:disabled):not(.disabled) {
		&:active:focus,
		&.active:focus {
			box-shadow: 0 0 0 2px rgba(248, 249, 250, 0.1);
		}
	}

	.show > .btn-light.dropdown-toggle:focus {
		box-shadow: 0 0 0 2px rgba(248, 249, 250, 0.1);
	}

	.btn-dark {
		color: $white;
		background-color: #343a40;
		border-color: #343a40;

		&:hover {
			color: $white;
			background-color: #23272b;
			border-color: #1d2124;
		}

		&:focus,
		&.focus {
			box-shadow: 0 0 0 2px rgba(52, 58, 64, 0.1);
		}

		&.disabled,
		&:disabled {
			color: $white;
			background-color: #343a40;
			border-color: #343a40;
		}

		&:not(:disabled):not(.disabled) {
			&:active,
			&.active {
				color: $white;
				background-color: #1d2124;
				border-color: #171a1d;
			}
		}
	}

	.show > .btn-dark.dropdown-toggle {
		color: $white;
		background-color: #1d2124;
		border-color: #171a1d;
	}

	.btn-dark:not(:disabled):not(.disabled) {
		&:active:focus,
		&.active:focus {
			box-shadow: 0 0 0 2px rgba(217, 221, 231, 0.1);
		}
	}

	.show > .btn-dark.dropdown-toggle:focus {
		box-shadow: 0 0 0 2px rgba(217, 221, 231, 0.1);
	}

	.btn-outline-default {
		color: #d5dae2;
		background-color: transparent;
		background-image: none;
		border: 1px solid rgba(255, 255, 255, 0.04);

		&:hover {
			color: #d5dae2 !important;
			background: rgba(255, 255, 255, 0.1);
			border: 1px solid rgba(255, 255, 255, 0.04);
		}

		&:focus,
		&.focus {
			box-shadow: 0 0 0 2px rgba(217, 221, 231, 0.1);
		}

		&.disabled,
		&:disabled {
			color: #435785;
			background-color: transparent;
		}

		&:not(:disabled):not(.disabled) {
			&:active,
			&.active {
				color: #d5dae2 !important;
				background: rgba(255, 255, 255, 0.1);
				border: 1px solid rgba(255, 255, 255, 0.04);
			}
		}
	}

	.show > .btn-outline-default.dropdown-toggle {
		color: #d5dae2 !important;
		background: rgba(255, 255, 255, 0.1);
		border: 1px solid rgba(255, 255, 255, 0.04);
	}

	.btn-outline-default:not(:disabled):not(.disabled) {
		&:active:focus,
		&.active:focus {
			box-shadow: 0 0 0 2px rgba(247, 45, 102, 0.1);
		}
	}

	.show > .btn-outline-default.dropdown-toggle:focus {
		box-shadow: 0 0 0 2px rgba(247, 45, 102, 0.1);
	}

	.btn-outline-light {
		color: $white;
		background-color: transparent;
		background-image: none;
		border-color: rgba(255, 255, 255, 0.1);
		box-shadow: none;

		&:hover {
			color: #c4c9d6;
			background-color: #20223c;
			border-color: #20223c;
		}

		&:focus,
		&.focus {
			box-shadow: 0 0 0 2px rgba(248, 249, 250, 0.1);
		}

		&.disabled,
		&:disabled {
			color: #cfd4e1;
			background-color: transparent;
		}

		&:not(:disabled):not(.disabled) {
			&:active,
			&.active {
				color: #c4c9d6;
				background-color: #132040;
				border-color: #132040;
			}
		}
	}

	.show > .btn-outline-light.dropdown-toggle {
		color: #c4c9d6;
		background-color: #132040;
		border-color: #132040;
	}

	.btn-outline-light:not(:disabled):not(.disabled) {
		&:active:focus,
		&.active:focus {
			box-shadow: 0 0 0 2px rgba(248, 249, 250, 0.1);
		}
	}

	.show > .btn-outline-light.dropdown-toggle:focus {
		box-shadow: 0 0 0 2px rgba(248, 249, 250, 0.1);
	}

	.btn-outline-dark {
		color: #343a40;
		background-color: transparent;
		background-image: none;
		border-color: #343a40;

		&:hover {
			color: $white;
			background-color: #343a40;
			border-color: #343a40;
		}

		&:focus,
		&.focus {
			box-shadow: 0 0 0 2px rgba(52, 58, 64, 0.1);
		}

		&.disabled,
		&:disabled {
			color: #343a40;
			background-color: transparent;
		}

		&:not(:disabled):not(.disabled) {
			&:active,
			&.active {
				color: $white;
				background-color: #343a40;
				border-color: #343a40;
			}
		}
	}

	.show > .btn-outline-dark.dropdown-toggle {
		color: $white;
		background-color: #343a40;
		border-color: #343a40;
	}

	.btn-outline-dark:not(:disabled):not(.disabled) {
		&:active:focus,
		&.active:focus {
			box-shadow: 0 0 0 2px rgba(52, 58, 64, 0.1);
		}
	}

	.show > .btn-outline-dark.dropdown-toggle:focus {
		box-shadow: 0 0 0 2px rgba(52, 58, 64, 0.1);
	}

	.btn-link {
		&:hover {
			color: #295a9f;
		}

		&:disabled,
		&.disabled {
			color: #868e96;
		}
	}

	.collapse:not(.show) {
		background: #272d47;
	}

	.dropdown-menu {
		color: #d5dae2;
		background-color: #272d47;
		border: 1px solid rgba(255, 255, 255, 0.04);
	}

	.dropdown-divider {
		border-top: 1px solid rgba(255, 255, 255, 0.04);
	}

	.notifyimg {
		color: $white;
	}

	.dropdown-item {
		color: #212529;
	}

	.drop-icon-wrap .drop-icon-item {
		color: #878787;

		&:hover,
		&:focus {
			background-color: rgba(32, 34, 60, 0.5);
		}
	}

	.dropdown-item {
		&:hover,
		&:focus,
		&.active,
		&:active {
			background-color: rgba(32, 34, 60, 0.5);
		}

		&.disabled,
		&:disabled {
			color: $white;
			background-color: transparent;
		}
	}

	.dropdown-menu-arrow {
		&.dropdown-menu-right,
		&.dropdown-menu-left {
			&:before {
				border-bottom: 10px solid rgba(255, 255, 255, 0.1);
				border-left: 10px solid transparent;
				border-right: 10px solid transparent;
			}

			&:after {
				border-bottom: 9px solid #272d47;
				border-left: 9px solid transparent;
				border-right: 9px solid transparent;
			}
		}
	}

	.dropdown-header {
		color: #d5dae2;
		border-bottom: 1px solid rgba(255, 255, 255, 0.04);
	}

	.dropdown-item-text {
		color: #212529;
	}

	.input-group-addon {
		color: #b7bec5;
		border: 1px solid rgb(72, 81, 103);
	}

	.input-group-text {
		color: #c4c9d6;
		background-color: rgba(32, 34, 60, 0.5);
		border: 1px solid rgba(255, 255, 255, 0.07);
    border-right: 1px solid rgba(255,255,255, 0.02);
	}

	#global-loader {
		background: #272d47;
	}

	.custom-control-input {
		&:checked ~ .custom-control-label::before {
			color: $white;
		}

		&:focus ~ .custom-control-label::before {
			box-shadow: 0 0 0 1px rgba(32, 34, 60, 0.5), 0 0 0 2px rgba(70, 127, 207, 0.25);
		}

		&:active ~ .custom-control-label::before {
			color: $white;
			background-color: #d4e1f4;
		}

